import React from 'react'
import millify from 'millify'
import {Typography, Row, Col, Statistic} from 'antd'
import {Link} from 'react-router-dom'
import {useGetCryptosQuery} from '../services/CryptoAPI'
import {Cryptocurrencies, News} from '../components'

import Loader from './Loader'

const {Title} = Typography;

const Homepage = () => {
  const { data, isFetching } = useGetCryptosQuery(10);
  const stats = data?.data?.stats;

  if(isFetching)
    return <Loader />;


  // console.log(data);
  return (
    <>
      <Title level={2} className="heading">
        Global Cryptoo Statistics
      </Title>
      <Row>
        <Col span={12}><Statistic title="Total Cryptocurrencies" value={stats.total} /></Col>
        <Col span={12}><Statistic title="Total Exchanges" value={millify(stats.totalExchanges)} /></Col>
        <Col span={12}><Statistic title="Total Market Cap" value={millify(stats.totalMarketCap)} /></Col>
        <Col span={12}><Statistic title="Total 24h Volume" value={millify(stats.total24hVolume)} /></Col>
        <Col span={12}><Statistic title="Total Market" value={millify(stats.totalMarkets)} /></Col>
      </Row>

      <div className="home-heading-container">
        <Title level={2} className="home-title">Top 10 Crypto Currencies in the world</Title>
        <Title level={3} className="show-more"><Link to="/Crypto-App/cryptocurrencies">Show More</Link></Title>
      </div>
      <Cryptocurrencies simplified />

      <div className="home-heading-container">
        <Title level={2} className="home-title">Latest news about Crypto Currencies</Title>
        <Title level={3} className="show-more"><Link to="/Crypto-App/news">Show News</Link></Title>
      </div>
      <News simplified />
    </>
  )
}

export default Homepage
